<template>
    <div class="theme_panel">
        <div class="toggle_bts" @click="setting = !setting">
            <i class="ion ion-android-settings"></i>
        </div>
        <div class="theme_menu" :class="{'active':setting}">
            <a class="buy-btn wp_btn" style="margin-top: 0;background: #248fef;">
                <i class="ion ion-hammer"></i>操控面板
            </a>
            <h4>主題色選擇</h4>
            <div class="segment">
                <ul class="theme layout_style">
                    <li v-for="(main, index) in mainColorList" :key="index">
                        <a
                            :title="'theme-'+main.name"
                            href="#"
                            @click="setMainColor(main.name)"
                            :class="{'main-broder':main.name==mainColor}"
                            :style="{'backgroundColor':main.color }"
                        ></a>
                    </li>
                </ul>
            </div>
            <div class="clear"></div>
            <h4>主面板選擇</h4>
            <ul class="theme demo_list dark_style">
                <li style="width: 50%;" @click="setIsDark(false)" :class="{'active':!isDark}">
                    <strong>Light</strong>
                </li>
                <li style="width: 50%;" @click="setIsDark(true)" :class="{'active':isDark}">
                    <strong>Dark</strong>
                </li>
            </ul>
            <div class="clear"></div>
            <h4>語系切換</h4>
            <ul class="theme demo_list">
                <li style="width: 50%;">
                    <a href="index-gradient.html">
                        <strong>繁中</strong>
                    </a>
                </li>
                <li style="width: 50%;">
                    <a href="index-bg-image.html">
                        <strong>簡中</strong>
                    </a>
                </li>
                <li style="width: 50%;">
                    <a href="index.html">
                        <strong>English</strong>
                    </a>
                </li>
                <li style="width: 50%;">
                    <a href="index-no-photo.html">
                        <strong></strong>
                    </a>
                </li>
            </ul>
            <div class="clear"></div>
            <h4>過場動畫選擇</h4>
            <ul class="theme demo_list">
                <li
                    v-for="(ani, index) in aniList"
                    :class="{'active':pageAni==ani.effect}"
                    :key="index"
                    style="width: 50%;"
                    @click="setAni(ani.effect)"
                >
                    <strong>{{ani.name}}</strong>
                </li>
            </ul>
            <div class="clear"></div>
        </div>
    </div>
</template>

<script>
import { mapState, mapMutations, mapActions } from "vuex";
export default {
    data() {
        return {
            setting: false,
            aniList: [
                { effect: "rotate", name: "旋轉" },
                { effect: "slide", name: "滑動" },
                { effect: "flip", name: "翻轉" },
                { effect: "roll", name: "滾動" },
                { effect: "fade", name: "淡出" }
            ],
            mainColorList: [
                { name: "green", color: "rgba(46, 202, 127, 1)" },
                { name: "blue", color: "rgba(8, 86, 193, 1)" },
                { name: "orange", color: "rgba(255, 152, 0, 1)" },
                { name: "pink", color: "rgba(255, 94, 148, 1)" },
                { name: "purple", color: "rgba(196, 70, 218, 1)" },
                { name: "red", color: "rgba(232, 103, 107, 1)" }
            ]
        };
    },
    computed: {
        ...mapState(["pageAni", "mainColor", "isDark"])
    },
    methods: {
        ...mapMutations(["setAni", "setMainColor", "setIsDark"])
    }
};
</script>

<style lang="scss" scoped>
li {
    cursor: pointer;
}
.main-broder {
    border: solid 4px #bbb;
}
</style>


