import Vuex from 'vuex';
import Vue from 'vue'
import * as web from './webUtil/storage';
Vue.use(Vuex)

let store = new Vuex.Store({
  state: {
    pageAni: web.getLocal(web.StorageKey.pageAni) || "roll",
    mainColor: web.getLocal(web.StorageKey.mainColor) || "green",
    isDark: web.getLocal(web.StorageKey.isDark) || false,
  },
  mutations: {
    setAni(state, ani) {
      if (web.setLocal(web.StorageKey.pageAni, ani)) {
        state.pageAni = ani;
      } else {
        console.error("Error:Can't Store pageAni");
      }
    },
    setMainColor(state, color) {
      if (web.setLocal(web.StorageKey.mainColor, color)) {
        state.mainColor = color;
      } else {
        console.error("Error:Can't Store mainColor");
      }
    },
    setIsDark(state, bool) {
      if (web.setLocal(web.StorageKey.isDark, bool)) {
        state.isDark = bool;
      } else {
        console.error("Error:Can't Store mainColor");
      }
    },
  },
  actions: {
    changeAni({
      context,
      state
    }, ani) {
      context.commit("setAni", ani)
    },
    changeMainColor({
      context,
      state
    }, color) {
      context.commit("setMainColor", color)
    },
    changeDarkTheme({
      context,
      state
    }, bool) {
      context.commit("setIsDark", bool)
    }
  }
})
export default store



